.sidebar{
    flex: 0.30;
    overflow-y: scroll;
}
.icons{
    font-size:1.5rem;
    color: gray;
}
.btn{
    border: none;
    cursor: pointer;
}
.sidebar_header{
    display:flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    align-items: center;
}
.sidebar_headerRight{
    display: flex;
    gap: 2rem;
}
.search_container{
    display: flex;
    align-items: center;
    padding: 1.3rem;
    background-color: white;
    width: fit-content;
    border-radius: 2rem;
    margin: 0 1rem;
}
.search_container > input{
    border: none;
    outline-style: none;
    outline-width: 0;
    
}
.logout{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2.5rem;
}
.logout > button{
    background-color: transparent;
    text-decoration: underline;
    color: blue;
    border: none;
    font-size: 1rem;
    cursor: pointer;
}
.create-btn{
    display: flex;
    text-align: center;
    border: none;
    margin: 1rem 0;
    cursor: pointer;
    padding: 1rem;
    width: 100%;
    transition: 0.2s ease-out;
}
.create-btn:hover{
    background-color: rgb(185, 185, 185);
}