*{
    font-family: sans-serif;
}
.login{
    text-align: center;
    position:static;
    width: 100%;
    height: 100%;
    z-index: 10;
    overflow-y: scroll;
}
.login_container{
    background-color: white;
    padding: 7rem 10rem;
}
.logo{
    background-color: white;
    border-radius: 5px;
    width: 30%;
    height: 30%;
}
.login_btn{
    border-radius: 6px;
    padding: 1rem 1.2rem;
    margin: 0 1rem;
    margin-bottom: 1rem;
    font-weight: 400;
    border: none;
    cursor: pointer;
}
.signin{
    background-color: rgb(21, 209, 21);
    color: white;
}
.register{
    background-color: gray;
}
.login_form{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.input > label{
    margin: 0 1.5rem;
}
.input > input{
    margin: 1rem 0;
    font-size: 1.5rem;
}
.create-link{
    margin-top: 2rem;
    color: blue;
}