.chat{
    flex: 0.70;
}
.chat_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0;
    padding: 0;
}
.chat_header_left{
    display: flex;
    margin: 1rem 0;
    gap: 1.5rem;
}
.chat_header_info > h4{
    margin: 0;
}
.chat_header_info > p{
    margin: 0;
}
.chat_header_right{
    display: flex;
    margin: 0 1rem;
    gap: 1.5rem;
}
.chat_body{
    background-image: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    flex: 1;
    background-repeat: repeat;
    background-position: center;
    overflow-y: scroll;
    height: 70vh;
}
.chat_message{
    margin: 0;
    display:grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    position: relative;
    margin: 1rem;;
    max-width: 50%;
}
.chat_message > span{
    background-color: white;
    padding: 0.5rem 0.9rem;
    margin: 0 1rem;
    width: fit-content;
    border-radius: 10px;
}
.chat_reciever{
    margin-left: auto;
    /* float: right; */
    /* width: fit-content; */
}
.chat_reciever > span{
    background-color: #dcf8c6;
}
/* .chat_reciever > strong{
    margin-right: auto;
} */
.chat_message > strong{
    margin: 0 1rem;
    /* position: absolute; */
}
.datetime{
    font-size: xx-small;
    margin: 0 1rem;
}
.chat_footer{
    display: flex;
    align-items: center;
    /* margin: 1rem; */
}
.search_outlined{
    display: flex;
    align-items: center;
    width:90%;
    margin-left: 0.7rem;
    background-color: white;
    border-radius: 10px;
    padding: 0.8rem;
}
.search_outlined > input{
    border: none;
    width: 100%;
    font-size: 1rem;
    outline-style: none;
    outline-width: 0;
    /* padding: 1rem 0.5rem; */
}
.btn{
    background-color: transparent;
}